const deleteHandler = (canvas, dispatch, active) => {
  if (active.id === 'subtitle') {
    dispatch({ type: 'setIsSubtitles', data: false });
  } else if (!active.isEditing && active.type !== 'video') {
    canvas.remove(active);
    canvas.discardActiveObject().requestRenderAll();
  }
};

export const keyDownHandler = (
  e,
  canvas,
  setIsPasting,
  setIsCopying,
  setIsBringToFront,
  setIsSendToBack,
  dispatch,
  setIsCut,
  setIsPlayPause,
  setIsPrevWord,
  setIsNextWord
) => {
  let isInput = document.activeElement.tagName === 'INPUT';
  if (!isInput) {
    let ctrl = e.ctrlKey || e.metaKey;
    if (
      e.code === 'Delete' ||
      e.key === 'Delete' ||
      e.code === 'Backspace' ||
      e.key === 'Backspace'
    ) {
      if (canvas) {
        let active = canvas.getActiveObject();
        if (active) deleteHandler(canvas, dispatch, active);
        else setIsCut(true);
      } else setIsCut(true);
    } else if (canvas && (e.code === 'Space' || e.key === 'Space')) {
      e.preventDefault();
      setIsPlayPause(true);
    } else if (canvas && (e.code === 'KeyC' || e.key === 'c') && ctrl) {
      e.preventDefault();
      setIsCopying(true);
    } else if (canvas && (e.code === 'KeyV' || e.key === 'v') && ctrl) {
      e.preventDefault();
      setIsPasting(true);
    } else if (canvas && (e.code === 'BracketRight' || e.key === ']') && ctrl) {
      e.preventDefault();
      setIsBringToFront(true);
    } else if (canvas && (e.code === 'BracketLeft' || e.key === '[') && ctrl) {
      e.preventDefault();
      setIsSendToBack(true);
    } else if (e.code === 'ArrowRight' || e.key === 'ArrowRight') {
      if (canvas) canvas.discardActiveObject();
      e.preventDefault();
      setIsNextWord(true);
    } else if (e.code === 'ArrowLeft' || e.key === 'ArrowLeft') {
      if (canvas) canvas.discardActiveObject();
      e.preventDefault();
      setIsPrevWord(true);
    }
  }
};

export const keyUpHandler = (
  setIsPasting,
  setIsCopying,
  setIsBringToFront,
  setIsSendToBack,
  setIsCut,
  setShouldRun,
  setIsPlayPause,
  setIsPrevWord,
  setIsNextWord
) => {
  setIsCopying(false);
  setIsPasting(false);
  setIsBringToFront(false);
  setIsSendToBack(false);
  setIsCut(false);
  setIsPlayPause(false);
  setIsPrevWord(false);
  setIsNextWord(false);
  setShouldRun(true);
};
